import { useState } from "react";
import  './dropdown.css';

const sendIcon = 'https://musical-florentine-3f6acd.netlify.app/img/send-svgr.svg';

const Title = ({ inputText, setInputText, handleSubmit, selectedOption, setSelectedOption, placeholders }) => {

    const handleSelectChange = (e) => {
        setSelectedOption(e.target.value);
    }
      
    return (
        <div className="col-12 col-md-12 col-lg-10" id="title">
            <form>
                <div class="row justify-content-center">

                <div class="col-6 col-md-4 col-lg-5 col-xl-3" id="dropdown1">
                    <div class="optionbox">
                        <select className="form-control"
                            value={selectedOption}
                            onChange={handleSelectChange}
                        >
                            {/* <option value="show">Show</option> */}
                            <option value="book">Book</option>
                            <option value="poem">Poem</option>
                            <option value="movie">Movie</option>
                            <option value="song">Song</option>
                        </select>
                    </div>
                </div>
                <div class="col-12 col-md-12 col-lg-9" id="text-input">
                    <input type="text" class="form-control"
                        onChange={(e) => setInputText(e.target.value)}
                        value={inputText}
                        placeholder={placeholders[selectedOption]}
                    />
                    <button id="enter-button" onClick={handleSubmit}>
                        <img src={sendIcon} alt='send icon' />
                    </button>
                </div>
                    {/* <div class="col-4">
                        <input type="text" class="form-control" placeholder="Author name" />
                    </div> */}
                </div>
                {/* <button class="btn btn-lg btn-primary" id="search">Analyze</button> */}
            </form>
        </div>

                    
        
    );
}

export default Title;